<!---


-->
<template>
<Frame :hideBack="true" title=" ">
  <Menu />
  <div class="tabs-header">
    <div id="sliderSegmentedControl" class="mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
			<a class="mui-control-item" :class="currentTab === 'query_order' && 'mui-active'" @click="changeToTab('query_order')">
				
			</a>
			<a v-if="user.user" class="mui-control-item" :class="currentTab === 'my_order' && 'mui-active'" @click="changeToTab('my_order')">
				
			</a>
				</div>
  </div>
  <div class="mui-content">
    <!----->
    <div v-show="currentTab === 'query_order'">
      <form class="mui-input-group">
        <div class="mui-input-row mui-row">
          <div class="mui-col-xs-8">
            <input type="text" class="mui-input" placeholder="">
          </div>
          <div class="mui-col-xs-4">
            <img height="100%" src="http://pubg.gotoip3.com/qwt/index.php/home/user/verifycode.html" />
          </div>
        </div>

        <div class="mui-input-row mui-row">
          <div class="mui-col-xs-8">
            
            <input type="text" v-model="keywords" class="mui-input-clear" placeholder="QQ//">
          </div>
          
          <div class="mui-col-xs-4">
            <button class="mui-btn mui-btn-primary" style="width: 100%" @click="queryOrder"> </button>
          </div>
        </div>
        
      </form>
      <p class="text-center" style="line-height: 40px">:~</p>
      
      <div class="query-order-results">
        <ul class="mui-table-view"> 
            <li v-for="order in orders" class="mui-table-view-cell mui-collapse mui-active">
                <a class="mui-navigate-right" href="#">{{order.status}}{{order.order_id}}     {{order.create_time}}</a>
                <div class="mui-collapse-content order-detail">
                    <p>: {{order.goods_name}}</p>
                    <p>: {{order.spec}}}</p>
                    <p>: {{order.price.rmb}} {{order.price.points}}</p>
                    <p>: {{order.amt}}</p>
                    <p>QQ: {{order.concat.qq}}</p>
                    <p>: {{order.create_time}}</p>
                    <p>: {{order.status}}</p>
                </div>
            </li>
        </ul>
      </div>
    </div>


  <!---->
    <div class="" v-show="currentTab === 'my_order'">
      :
      <div class="query-order-results">
        <ul class="mui-table-view"> 
            <li v-for="order in orders" class="mui-table-view-cell mui-collapse">
                <a class="mui-navigate-right" href="#">{{order.status}}{{order.order_id}}     {{order.create_time}}</a>
                <div class="mui-collapse-content">
                    <p>: {{order.goods_name}}</p>
                    <p>: {{order.spec}}}</p>
                    <p>: {{order.price.rmb}} {{order.price.points}}</p>
                    <p>: {{order.amt}}</p>
                    <p>QQ: {{order.concat.qq}}</p>
                    <p>: {{order.create_time}}</p>
                    <p>: {{order.status}}</p>
                </div>
            </li>
        </ul>
        <div class="mui-content-padded">
				<ul class="mui-pager">
					<li>
						<a href="#">
							
						</a>
					</li>
					<li>
						<a href="#">
							
						</a>
					</li>
				</ul>
			</div>
      </div>
    </div>
  </div>
</Frame>
</template>
<script>
import {mapState} from 'vuex';
import $ from 'axios';
import {guest as GUEST_API} from '@/config/serverApi';

import Menu from '@/pages/Index.vue';
import Frame from '@/components/Frame.vue';

export default {
  components: {
    Menu,
    Frame
  },
  computed: {
     ...mapState({
       user: state => state.user
     })
   },
  data() {
    return {
      currentTab: 'query_order',
      keywords: '',
      orders: [
      ]
    };
  },
  methods: {
    changeToTab(tab) {
      if (tab === this.currentTab) return false;
      this.currentTab = tab;
    },
    queryOrder(e) {
      e.preventDefault();
      this.$loading.show('...');
      $.post(GUEST_API.getOrderByVisitor, {keywords: this.keywords})
        .then(res => {
          this.$loading.hide();
          if (res.data.status === 0) {
            this.$tip.show(res.data.msg);
          }
          const {concat, price} = res.data;
          
          this.orders = res.data;
        })
        .catch(err => {
          this.$loading.hide();
          this.$tip.show('');
          console.log(err);
        });
    }
  },
  mounted() {
  }
};
</script>
<style>
.mui-input-group {
  line-height: 40px;
}
.order-detail p {
  line-height: 0.5rem;
}
</style>


